* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  background-color: rgb(250, 250, 250);
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.timer {
  width: 500px;
  height: 125px;
  background-color: rgba(24, 23, 23, 0.171);

  border-radius: 5px;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2) inset;

  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;

  margin-top: 10px;
}

.timer .field {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.timer .field .value {
  font-size: 55px;
  color: rgb(250, 246, 246);
  background-color: rgba(0, 0, 0, 0.8);

  border-radius: 8px;
  padding: 0 10px;
}

.timer .field .label {
  font-size: 15px;
  color: rgba(15, 15, 15, 0.5);

  margin-top: 4px;
}
